/*
     fields     id  json
    
   fatalError      
 */

function getDataFromNode(id, fields) {
  const res = {};
  res.fatalError = false;

  try {

    let info = document.getElementById(id);

    info = JSON.parse(info.innerHTML);

    if (fields === 'ALL') {

      for (let key in info) {
        res[key] = info[key];
      }

    } else {

      fields.forEach( (field) => {
        if (info[field] === undefined) {
          console.error('    ' + field + '.');
          res.fatalError = true;
          return res;
        }

        res[field] = info[field];
      });
    }

  } catch(e) {
    res.fatalError = true;

    switch (e.name) {

      case 'SyntaxError': 
        console.error(' JSON   .');
        break;

      case 'TypeError':
        console.error('      id = ' + id + '.');
        break;

      default:
        console.error(e);
    }
  }

  return res;
}

export default getDataFromNode;


